import React from 'react';

import Wrapper from 'components/atoms/Wrapper';
import PageTitle from 'components/atoms/PageTitle';
import Section from 'components/atoms/Section';
import Headline from 'components/atoms/Headline';
import Paragraph from 'components/atoms/Paragraph';
import AboutList from 'components/molecules/AboutList';
import { generateUnicId } from 'helpers';

const aboutData = [
  {
    key: generateUnicId(),
    title: 'szybkie dodawanie transakcji',
    label: 'dodawnanie nowych transakcji w prosty spoww.',
  },
  {
    key: generateUnicId(),
    title: 'szybki wgld w wydadki',
    label: 'moliwo dodawania wydatkw w cigu dnia.',
  },
  {
    key: generateUnicId(),
    title: 'pogldowe wykresy',
    label: 'przegldaj dochody i wydatki w sposb graficzny.',
  },
  {
    key: generateUnicId(),
    title: 'kategorie',
    label: 'dodawaj wasne kategorie wydatkw.',
  },
  {
    key: generateUnicId(),
    title: 'dark mode',
    label: 'moliwo dostosowania wygldu aplikacji do swoich preferencji.',
  },
  {
    key: generateUnicId(),
    title: 'Notatki',
    label: 'moliwo dostosowania wasnych notatek.',
  },
];

const AboutPage = () => {
  return (
    <Wrapper withVariants>
      <PageTitle screenRenderOnly>o nas</PageTitle>
      <Section>
        <Paragraph>
          Aplikacja ta pokae Ci, na co wydajesz swoje pienidze. ledzenie finansw na pewno nie
          naley do najprzyjemniejszych zada na wiecie i wymaga pewnych zdolnoci oraz wiedzy.
          Dlatego stworzyem j, aby pomc Ci, w kadej chwili, ledzi swoje wydatki.
        </Paragraph>
        <Paragraph withMargin="2">
          Aplikacja w wygodny sposb pozwoli kontrolowa wydawane pienidze.
        </Paragraph>
      </Section>

      <Section>
        <Headline customFontSize={2.5}>Najwaniejsze funkcje</Headline>
        <AboutList listItemsData={aboutData} />
      </Section>
    </Wrapper>
  );
};

export default AboutPage;
